@Component({
      components: {Tabs: Tabs1, Notes, Types, NumberPad, Tags},
      // computed: {//数据不管是对象和值都要放在这里,当值变化时对这些数据进行监听，保证数据的及时和灵活
      //   recordList () {return this.$store.state.recordList},
      // }
    },
)
export default class Money extends Vue {

  typeList = typeList;
  // eslint-disable-next-line no-undef
  record: RecordItem = {tags: [], notes: '', type: '+', amount: 0, createdAt: undefined};

  // eslint-disable-next-line no-undef
  get recordList() {
    return this.$store.state.recordList;
  }


  created() {
    this.$store.commit('fetchRecords');
  }

  onUpdateNotes(value: string) {
    this.record.notes = value;
  }

  onUpdateAmount(value: string) {
    this.record.amount = parseFloat(value);
  }

  onUpdateTags(value: Tag[]) {
    this.record.tags = value;
  }

  saveRecord() {
    try {
      if(!this.record.tags||this.record.tags.length===0){
       return  window.alert('至少选择一个标签')
      }
      this.$store.commit('createRecord', this.record);
      if(this.$store.state.createRecordError===null){
        window.alert('创建成功')
        this.record.notes=''
      }
    } catch (error) {
      console.log('saveRecord');
      console.log(error);
    }
  }


}
